// src/components/Input/InputText.jsx
function InputText({
  inputType,
  labelTitle,
  labelStyle = "",
  containerStyle = "",
  value = '',
  placeholder,
  updateFormValue,
  updateType,
  border,
  size,
  labelPosition = "top",
  hasError = false
}) {
  let borderStyle = border ? `input-${border}` : "input-bordered";
  let sizeStyle = size ? `input-${size}` : "input-md";
  return (
    <div 
      className={`form-control ${containerStyle}`}
      style={{
        ...(labelPosition === "left" && {
          flexDirection: "row",
          alignItems: "center",
        }),
        ...(labelPosition === "top" && {
          flexDirection: "column",
          alignItems: "flex-start",
        }),
      }}
    >
      <label
        className={`label ${
          labelPosition === "left" ? "w-1/4" : "w-full"
        }`}
        style={{
          marginRight: labelPosition === "left" ? "1rem" : "0",
        }}
      >
        <span className={`label-text ${hasError ? 'text-red-500' : 'text-base-content'} ${labelStyle}`}>
          {labelTitle}
        </span>
      </label>
      <input
        type={inputType || "text"}
        value={value ?? ''}
        placeholder={placeholder || ""}
        onChange={(e) => updateFormValue(e.target.value)} // ✅ se envía solo el valor
        className={`input ${borderStyle} ${sizeStyle} ${labelPosition === "left" ? "w-3/4" : "w-full"
        } ${hasError ? 'border-red-500' : ''}`}
      />
    </div>
  );
}

export default InputText;
